@import '../../Public/tools.scss';

.c-dashboard_budget {
  width: calc(100%/3);
  max-width: 220px;
  min-width: 170px;
  padding: $spacing_100;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  cursor: pointer;
}

.c-dashboard_budget h2 {
  margin-bottom: 0;
}

.c-dashboard_budget__chart_wrapper {
  position: relative;
}

$chart_size: calc(100% - #{$spacing_200});

.c-dashboard_budget__chart_nums {
  position: absolute;
  width: $chart_size;
  top: 0;
  background: $white;
  border-radius: 100%;
  height: calc(#{$chart_size} - 4px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: $spacing_100;
  margin-left: $spacing_100;
}
.c-dashboard_budget__chart_nums h1 {
  margin: 0;
  margin-bottom: $spacing_50;
  font-size: $text_large;
}
.c-dashboard_budget__chart_nums h2 {
  margin: 0;
  font-size: $text_body;
}

@media (max-width: 730px) {
  .c-dashboard_budget__chart_nums h1 {
    font-size: $text_med,
  }
  .c-dashboard_budget__chart_nums h2 {
    margin: 0;
    font-size: $text_small;
  }
}



