@import '../../Public/tools.scss';

.c-progress {
  width: 100%;
  height: 100px;
  position: relative;
  border-radius: 8px;
}
.c-progress.c-card {
  padding: 0;
}
.c-progress__so_far {
  height: 100%;
  background: $dark_blue;
  border-right: 2px solid $dark_blue;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  width: 0px;
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
}
.c-progress__left_wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: $white;
  -ms-flex-pack: center;
      justify-content: center;
}
.c-progress__left {
  width: 100%;
  text-shadow: 0 0 1px rgba(0,0,0,0.5);
}
.c-progress__left--dark {
  color: $dark_gray;
  text-shadow: none;
  position: relative;
  background: fade-out($white, .5) ;
  width: 100px;
  border-radius: 3px;
}
.c-progress__left h1 {
  margin: 0;
  font-size: $text_large;
  padding: 0;
  margin-bottom: $spacing_50;
}
.c-progress__left h2 {
  margin: 0;
  font-size: $text_body;
  padding: 0;
}



