// Colors
$black: #000000;
$dark_gray: #484848;
$gray: #767676;
$light_gray: rgba(0,0,0,0.1);
$white: #ffffff;

$light_blue: #95E5F5;
$dark_blue: #074E93;
$pink: #FF585B;
$dark_green: #00848A;
$light_green: #D3F2B3;

// Spacing
$spacing_25: 4px;
$spacing_50: 8px;
$spacing_75: 12px;
$spacing_100: 16px;
$spacing_125: 20px;
$spacing_150: 24px;
$spacing_175: 28px;
$spacing_200: 32px;

$border_radius: 4px;

// fontface
$text_large: 24px;
$text_med: 22px;
$text_body: 16px;
$text_small: 12px;

@mixin max-width {
  max-width: 1000px;
  padding: 0 $spacing_100;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

input {
  width: 100%;
  padding: $spacing_100 $spacing_150;
  margin-bottom: $spacing_100;
  box-sizing: border-box;
  border-radius: $border_radius;
  border: 1px solid $black;
  font-size: 14px;
}
input[type="checkbox"] {
  width: auto;
  margin: 0;
}

button {
  background-color: $dark_blue;
  border: none;
  color: #fff;
  box-shadow: 0 5px 0 rgba(17,17,31,.25);
  border-radius: 99999rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  padding: $spacing_75 $spacing_200;
  cursor: pointer;
  transition: .1s;

  &:hover {
    background: $black;
    box-shadow: 0 0px 0 rgba(17,17,31,.25);
    transition: .1s;
  }
}

.c-card {
  width: 100%;
  padding: $spacing_200;
  box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.06);
  border-radius: calc(#{$border_radius} * 2);
  background: $white;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid $light_gray;
}
.c-card_header {
  width: calc(100% + 64px);
  // background: linear-gradient(-90deg, #26C7B1 0%, #2FA9C2 100%);
  color: $dark_gray;
  margin-left: -$spacing_200;
  margin-top: -$spacing_200;
  margin-bottom: $spacing_200;
  padding: $spacing_100 $spacing_200;
  border-top-left-radius: calc(#{$border_radius} * 2);
  border-top-right-radius: calc(#{$border_radius} * 2);
  box-sizing: border-box;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 14px;
  border-bottom: 1px solid $light_gray;
}
.c-card.c-clickable_card {
  transition: .2s;
}
.c-clickable_card:hover {
  transform: scale(1.01);
  transition: .2s;
}

.c-light_text {
  color: $dark_gray;
}
.c-error_text {
  color: $pink;
}



