@import '../../Public/tools.scss';

.p-nav {
  width: 100%;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: $spacing_100;
  color: $dark_gray;
  font-weight: bold;
  font-size: $text_body;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: $white;
  z-index: 1;
  margin-bottom: $spacing_200;
}
.p-nav--logged_out {
  border-bottom: 1px solid $dark_blue;
  margin-bottom: 0;
}
.p-nav--logged_in {
  border-bottom: 1px solid $light_gray;
}
.p-nav__logo {
  color: $dark_blue;
}
.p-nav__items {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.p-nav__item {
  margin: 0 $spacing_150;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 2px solid $white;
  cursor: pointer;

  &:hover {
    border-bottom: 2px solid $gray;
  }
}
.p-nav--logged_in .p-nav__item {
  color: $black;
}

.p-nav_actions {
  width: 70px;
  height: 100vh;
  background: $dark_blue;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
}

@media (max-width: 350px) {
  .p-nav--logged_in {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    padding: $spacing_100 inherit;
  }
}



